<script>
// Utilitites
import { scrollto } from "svelte-scrollto";
import { sendEmail, validateEmail } from "./utils/email";

// Components
import Button from "./components/Button.svelte";
import ProjectCard from "./components/ProjectCard.svelte";
import Award from "./components/vectors/Award.svelte";
import List from "./components/vectors/List.svelte";
import Message from "./components/vectors/Message.svelte";
import Globe from "./components/vectors/Globe.svelte";

// i18n
import {
  addMessages,
  locale,
  init,
  getLocaleFromNavigator,
  format,
} from "svelte-i18n";

import en from "./translations/en.json";
import ptBr from "./translations/ptBR.json";

addMessages("en-US", en);
addMessages("pt-BR", ptBr);

init({
  fallbackLocale: "pt-BR",
  initialLocale: getLocaleFromNavigator(),
});

const handleChangeLang = () => {
  if ($locale === "en-US") locale.set("pt-BR");
  else locale.set("en-US");
};

// Images/Vectors
let imgs = {
  logo: "images/logo.svg",
  ellipse_1: "images/ellipse_1.svg",
  avatar: "images/avatar.svg",
  message: "images/feather_message-circle.svg",
  arrow_down: "images/arrow-down.svg",
  decoration: "images/decoration.svg",
  decoration_en: "images/decoration_en.svg",
  glyde_frame: "images/glyde_frame.png",
  simresolvemos_frame: "images/simresolvemos_frame.png",
  clients: "images/clients.svg",
  footer_bg: "images/footer_bg.png",
  github: "images/github.svg",
  gmail: "images/gmail.svg",
  linkedin: "images/linkedin.svg",
};

// Variables
let services = [
  {
    name: "Desktop",
    icon: "images/feather_monitor.svg",
    description: "services.desktop",
  },
  {
    name: "Mobile",
    icon: "images/feather_smartphone.svg",
    description: "services.mobile",
  },
  {
    name: "Web",
    icon: "images/feather_globe.svg",
    description: "services.web",
  },
  {
    name: "Design",
    icon: "images/feather_pen-tool.svg",
    description: "services.design",
  },
];

let service_steps = [
  {
    name: "services.steps.contact.header",
    description: "services.steps.contact.description",
  },
  {
    name: "services.steps.pricing.header",
    description: "services.steps.pricing.description",
  },
  {
    name: "services.steps.development.header",
    description: "services.steps.development.description",
  },
];

let navLinks = [
  { name: "services.header", icon: List, selector: "#services" },
  { name: "portfolio", icon: Award, selector: "#projects" },
  { name: "contact.header", icon: Message, selector: "#contact" },
];

// Form
let formError = false;
let formSuccess = false;

let from_name = "";
let from_email = "";
let from_message = "";

const handleFormSubmit = () => {
  formSuccess = false;

  if (!from_name || !from_email || !from_message) {
    formError = "contact.errors.missingFields";
    return;
  }
  if (!validateEmail(from_email)) {
    formError = "contact.errors.invalidEmail";
    return;
  }

  formError = false;

  sendEmail(from_name, from_email, from_message);

  formSuccess = true;
};
</script>

<style type="text/scss">nav {
  position: relative;
}
nav .ellipse_1 {
  position: absolute;
  right: 0;
  top: -50%;
  transform: translate(19rem, -55%);
  z-index: 0;
}

#landing .name {
  position: relative;
  border-bottom: 3px #48dbfb solid;
}
#landing .name img {
  display: block;
  position: absolute;
  top: 0;
  right: 0;
}

footer {
  position: relative;
}

footer .bg {
  width: 2561px;
  height: 250px;
  margin-top: 100px;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
}
footer .bg img {
  width: 100%;
}</style>

<main
  class="min-h-screen bg-night-black font-body antialiased text-ghost-white
  relative overflow-hidden">
  <div class="container mx-auto">
    <nav
      class="flex flex-col md:flex-row justify-center items-center
      md:justify-between p-4">
      <div class="flex items-center">
        <img src={imgs.logo} alt="glyde" />
      </div>
      <div class="flex justify-between items-center z-10">
        <button
          on:click={handleChangeLang}
          class="flex items-center ml-2 md:ml-0 md:mr-8 mt-2 md:mt-px
          text-fiery-rose font-semibold font-header">
          {$locale}
          <Globe extraClass="stroke-current text-fiery-rose w-8 h-8" />
        </button>
        {#each navLinks as link}
          <a
            href={link.selector}
            class="hidden md:block text-ghost-white text-lg font-body mr-5
            last:mr-0"
            use:scrollto={link.selector}>
            {$format(link.name)}
          </a>
        {/each}
      </div>
      <img src={imgs.ellipse_1} alt="ellipse" class="ellipse_1" />
    </nav>

    <div
      id="landing"
      class="flex justify-center align-center relative mt-8 md:mt-0 lg:py-40">
      <div class="flex items-center flex-col lg:flex-row">
        <img
          src={imgs.avatar}
          alt="Foto de Thomas"
          class="mr-0 lg:mr-20 mb-8 lg:mb-0 w-64 sm:w-auto" />
        <div class="max-w-2xl text-center lg:text-left">
          <p
            class="font-header text-xl sm:text-3xl md:text-4xl lg:text-4.5xl
            font-semibold m-0">
            <span
              class="font-bold text-3xl sm:text-5xl md:text-6xl lg:text-8xl">
              {$format('landing.hi')}
            </span>
            {$format('landing.introduction')}
            <span class="name">
              Thomas.
              <img
                src={$locale === 'en-US' ? imgs.decoration_en : imgs.decoration}
                alt="Designer e Desenvolvedor"
                class="transform scale-175 sm:scale-150 -translate-y-full" />
            </span>
          </p>
          <p class="font-body text-2xl sm:text-4xl md:text-5xl font-light m-0">
            {$format('landing.slogan')}
          </p>

          <Button
            src={imgs.arrow_down}
            alt="Seta para baixo"
            innerText={$format('landing.cta')}
            extraClass="pr-8 mx-auto lg:mx-0 mt-12"
            scrollTo={'#services'} />

        </div>
      </div>
    </div>

    <div id="services" class="pt-16">
      <h1
        class="font-header font-semibold text-5xl my-8 ml-0 sm:ml-4 text-center
        sm:text-left">
        {$format('services.header')}
      </h1>

      <div class="flex justify-between flex-row flex-wrap">
        {#each services as service}
          <div
            class="bg-eerie-black mx-4 mb-5 px-12 py-8 flex-1 flex flex-col
            justify-center items-center rounded">
            <img src={service.icon} alt="Monitor" />
            <div class="flex flex-col items-center mt-4">
              <span class="font-header text-2xl">{service.name}</span>
              <span class="font-body font-light text-lg text-center">
                {$format(service.description)}
              </span>
            </div>
          </div>
        {/each}
      </div>

      <div class="flex flex-wrap justify-between mt-16 ">
        {#each service_steps as step, i}
          <div class="flex-1 mx-4 flex flex-col mb-4">
            <span class="flex items-center">
              <h1
                class="m-0 mr-4 font-header font-bold text-5xl text-fiery-rose
                text-opacity-75">
                {i + 1}.
              </h1>
              <h2 class="font-header font-semibold text-2xl sm:text-3xl">
                {$format(step.name)}
              </h2>
            </span>
            <span class="font-body text-lg">{$format(step.description)}</span>
          </div>
        {/each}
      </div>

      <Button
        src={imgs.message}
        alt="Ícone mensagem"
        innerText={$format('services.cta')}
        extraClass="mx-auto mt-12"
        scrollTo={'#contact'} />

    </div>

    <div id="projects" class="pt-32 ">
      <h1
        class="font-header font-semibold text-5xl my-8 ml-0 sm:ml-4 text-center
        sm:text-left">
        {$format('portfolio')}
      </h1>

      <div class="flex flex-col sm:flex-row mx-4 mt-0 sm:-mt-24">
        <ProjectCard
          background="bg-gradient-to-br from-fiery-rose to-transparent"
          extraClass="mt-0 sm:mt-32"
          title="Glyde"
          technologies={['Svelte', 'Sass']}
          img={imgs.glyde_frame}
          url="https://www.glyde.com.br" />
        <ProjectCard
          background="bg-gradient-to-br from-blue-500 to-transparent"
          extraClass="mt-8 sm:mt-0"
          title="SimResolvemos"
          technologies={['Django', 'Bulma', 'jQuery']}
          img={imgs.simresolvemos_frame}
          url="https://www.simresolvemos.com.br" />

      </div>
    </div>

    <div id="testimonies" class="mt-32 lg:mt-48 px-8">
      <div class="flex justify-between items-center">
        <p
          class="relative max-w-xl font-body font-light text-xl sm:text-3xl z-10">
          <span
            class="absolute top-0 -mt-20 -ml-2 font-semibold font-cursive
            text-7xl text-columbia-blue opacity-50 transform rotate-180">
            "
          </span>
          {$format('testimonies.sr')}
          <span class="block font-cursive font-normal mt-12">
            Daniel,
            <span class="font-bold text-fiery-rose">SimResolvemos</span>
          </span>
        </p>

        <img src={imgs.clients} alt="Clientes" class="hidden lg:block" />

      </div>
    </div>

    <div id="contact" class="pt-32 mx-2">
      <div class="bg-eerie-black rounded max-w-4xl px-4 sm:px-12 py-8 mx-auto">
        <h1
          class="font-header font-semibold text-2xl sm:text-4xl text-center
          sm:text-left">
          {$format('contact.question')}
        </h1>
        <form class="mt-4">
          <label class="block">
            <span class="font-light text-lg sm:text-2xl">
              {$format('contact.name')}
            </span>
            <input
              class="form-input text-night-black mt-2 block w-full"
              placeholder="Thomas G Lopes"
              bind:value={from_name} />
          </label>
          <label class="block mt-4">
            <span class="font-light text-lg sm:text-2xl">E-mail</span>
            <input
              class="form-input text-night-black mt-2 block w-full"
              type="email"
              placeholder="thomas@glyde.com.br"
              bind:value={from_email} />
          </label>
          <label class="block mt-4">
            <span class="font-light text-lg sm:text-2xl">
              {$format('contact.description')}
            </span>
            <textarea
              class="form-textarea text-night-black mt-2 block w-full"
              rows="5"
              placeholder={$format('contact.description_placeholder')}
              bind:value={from_message} />
          </label>

          <Button
            src={imgs.message}
            alt="Ícone Mensagem"
            innerText={$format('contact.button')}
            extraClass="pr-8 mx-auto lg:mx-0 mt-8"
            textSize="text-xl"
            onClick={() => {
              handleFormSubmit();
            }} />

          {#if formError}
            <p class="text-fiery-rose mt-4">{$format(formError)}</p>
          {/if}

          {#if formSuccess}
            <p class="text-megaman-blue mt-4">{$format('contact.success')}</p>
          {/if}

        </form>
      </div>
    </div>

  </div>
  <footer class="relative flex flex-col justify-center mt-16">
    <div class="inline-flex justify-between items-center mx-auto w-auto z-10">
      <a
        href="https://github.com/TGlide"
        target="_blank"
        class="hover:opacity-75 transition ease-in duration-75 w-12 mr-8">
        <img src={imgs.github} alt="Github" />
      </a>
      <a
        href="mailto:thomas@glyde.com.br"
        target="_blank"
        class="hover:opacity-75 transition ease-in duration-75 w-12 mr-8">
        <img src={imgs.gmail} alt="Gmail" />
      </a>
      <a
        href="https://www.linkedin.com/in/thomas-gl/"
        target="_blank"
        class="hover:opacity-75 transition ease-in duration-75 w-12">
        <img src={imgs.linkedin} alt="Linkedin" />
      </a>
    </div>

    <hr class="block w-64 mx-auto mt-5" />

    <span class="text-ghost-white text-xl font-light text-center mt-5 mb-48">
      {$format('footer.love')}
      <span class="text-fiery-rose text-2xl font-cursive pr-1">&lt;3</span>
      {$format('footer.by')}
    </span>

    <div class="bg">
      <img src={imgs.footer_bg} alt="Ondas" class="z-0" />
    </div>
  </footer>

</main>
